import { HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { RacCommonUtil } from '../util/rac-util';
import { of } from 'rxjs/observable/of';
import { MessagerService } from '@farris/ui-messager';
import { HierarchicalType } from '../entity/hierarchical-type.enum';


/** åæ°æå¡ */
@Injectable()
export class TreeService {
    constructor(
        // private rest: RestfulService,
        private util: RacCommonUtil,
        // private msgService: MessagerService,
    ) {
    }

    /** æç§pathç æé æ  */
    makeTreeByPath(datas: any[], pathField: string ): any [] {
        // ä¸ç´ç²ç©ºï¼æèæ²æä¸ç´ç
        const pathLayerOnes = datas.filter(x => {
            if ( this.util.isNullOrEmptyString(x[pathField]) ) {
               return true;
            }
            const findObj = datas.find(parent => parent[pathField] === x[pathField].substring(0, x[pathField].length - 4 ));
            if ( findObj === undefined || findObj === null ) {
               return true;
            }

        });
        const treedata = [];
        pathLayerOnes.forEach(x => {
                const entity = {};
                entity['data'] = x;
                entity['children'] = [];
                treedata.push(entity);
                // this.root = x['id'];
        });
        treedata.forEach( item => {
                this.findChildrenByPath(item, datas, pathField);
            }
        );
        return treedata;
    }

    /** æ¾å°å­èç¹ */
    findChildrenByPath(parent: any, datas: any[], pathField: string) {
    const childList = datas.filter(childitem => {
          if (childitem[pathField].substring(0, childitem[pathField].length - 4) === parent['data'][pathField]) {
              return true;
          }
        });
    if (childList && childList.length > 0) {
      childList.forEach(child => {
        const childEntity = {};
        childEntity['data'] = child;
         // tslint:disable-next-line:no-string-literal
        childEntity['children'] = [];
         // tslint:disable-next-line:no-string-literal
        parent['children'].push(childEntity);
        this.findChildrenByPath(childEntity, datas, pathField);
      });
    }
  }

   /** æç§parentç æé æ  */
   makeTreeByParent(datas: any[], parentField: string, keyField: string): any [] {
    // ä¸ç´ç²ç©ºï¼æèæ²æä¸ç´ç
    const pathLayerOnes = datas.filter(x => {
       if ( this.util.isNullOrEmptyString(x[parentField])) {
           return true;
       }
       // æ­å±çè¿ç§ï¼åºè¯¥ä¹å±äºä¸çº§
       const p = datas.find(parent => parent[keyField] === x[parentField]);
       if (!p) {
         return true;
       }
    });
    const treedata = [];
    pathLayerOnes.forEach(x => {
            const entity = {};
            entity['data'] = x;
            entity['children'] = [];
            treedata.push(entity);
            // this.root = x['id'];
    });
    treedata.forEach( item => {
            this.findChildrenByParent(item, datas, parentField, keyField);
        }
    );
    return treedata;
    }

    /** æ¾å°å­èç¹ */
    findChildrenByParent(parent: any, datas: any[], parentField: string, keyField: string) {
        const childList = datas.filter(childitem => {
            if (childitem[parentField] === parent['data'][keyField]) {
                return true;
            }
        });
        if (childList && childList.length > 0) {
          childList.forEach(child => {
            const childEntity = {};
            childEntity['data'] = child;
            childEntity['children'] = [];
            parent['children'].push(childEntity);
            this.findChildrenByParent(childEntity, datas, parentField, keyField);
          });
        }
    }
}
