import { HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { RacCommonUtil } from '../util/rac-util';
import { of } from 'rxjs/observable/of';
import { MessagerService } from '@farris/ui-messager';

/** åæ°æå¡ */
@Injectable()
export class AuthfieldService {
    constructor(
        private rest: RestfulService,
        private util: RacCommonUtil,
        private msgService: MessagerService,
    ) {
    }

    /**æ ¹æ®æéå­æ®µçæ è¯è·åæéå­æ®µ
     * @param authfield æéå­æ®µæ è¯
     */
    getbyId(authfield: string): Observable<any> {
        const headers = this.util.getHeader();
        const url = this.util.getAuthfieldPath() + `/` + authfield;
        return this.rest.get(url, null, headers);
    }


    /**
     * å¼å¸¸å¤ç
     * @param auobj æéå¯¹è±¡å®ä½
     */
    handlerError(error: HttpErrorResponse) {
        console.log(error);
        this.msgService.error(error.error.Message);
    }

    private handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {
            console.error(error); // log to console instead
            return of(result as T);
        };
    }
}
