import {
  Component, OnInit, ViewChild, ComponentFactoryResolver, Injector,
  TemplateRef, HostBinding, AfterViewChecked, Output, Input, EventEmitter
} from '@angular/core';
import { Observable, from } from 'rxjs';
import { LookupComponent } from '@farris/ui-lookup';
import { ReAssignEntity } from '../../../common/entity';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'rac-reassign-help',
  templateUrl: './reassign-help.component.html',
  styleUrls: ['./reassign-help.component.css']
})
export class ReassignHelpComponent implements OnInit {

  @Input() displayText = '';
  @Input() curKeyId: string;
  @Input() datas: ReAssignEntity[];
  curKeyIdList: string[];
  curObj: ReAssignEntity;
  selectedKeyOld = '';

  @ViewChild('reassignLookup') reassignLookup: LookupComponent;

  /** å¯¼èªäºä»¶ */
  @Output() selectedChanged: EventEmitter<ReAssignEntity> = new EventEmitter<ReAssignEntity>();

  constructor() { }

  ngOnInit() {
  }

   // åæ°å
   afterKeySelected(args: any) {
    this.curObj = new ReAssignEntity();
    this.curKeyIdList = [];
    // æ´æ°å¼
    this.curObj.id = args['id'];
    this.curObj.authorizationCode = args['authorizationCode'];
    this.curObj.authorizationName = args['authorizationName'];
    this.curObj.authfieldCode = args['authfieldCode'];
    this.curObj.authfieldName = args['authfieldName'];
    this.displayText = args['authorizationName'] + ' ' + args['authfieldName'] ;
    this.selectedChanged.emit(this.curObj);
    this.reassignLookup.closeDialog();
}


/**
 * éä¸­è¡æ¹ååè§¦åäºä»¶
 */
handleSelection(args: any) {
  this.curKeyId = args.node.data.id;
  this.curObj = args.node.data;
  // é¦æ¬¡è§¦å
  if (this.curKeyId === '') {
    this.selectedKeyOld = this.curKeyId;
    this.selectedChanged.emit(this.curObj);
  } else {
    // ååçæåµä¸è§¦å
    if (this.selectedKeyOld !== this.curKeyId) {
      this.selectedKeyOld = this.curKeyId;
      this.selectedChanged.emit(this.curObj);
    }
  }
}

}
