import {
  Component, OnInit, ViewChild, ComponentFactoryResolver, Injector,
  TemplateRef, HostBinding, AfterViewChecked, Output, Input, EventEmitter
} from '@angular/core';
import { Observable, from } from 'rxjs';
import { LookupComponent } from '@farris/ui-lookup';
import { AuthobjEntity } from '../../../common/entity/authobj.entity';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'rac-auobj-help',
  templateUrl: './auobj-help.component.html',
  styleUrls: ['./auobj-help.component.css']
})
export class AuobjHelpComponent implements OnInit {


    /** è¦å è½½çæ°æ®ç±»å
     * allä»£è¡¨å¨é¨æéå¯¹è±¡
     * reassignä»£è¡¨åè®¸å¨ç¨æ·ä¸éæ°ææçæéå¯¹è±¡
     */
  @Input() dataMode: 'all' | 'reassign';
  @Input() displayText = '';
  @Input() curKeyId: string;
  curKeyIdList: string[];
  curAuthObj: AuthobjEntity;
  selectedKeyOld = '';

  @ViewChild('auobjLookup') auobjLookup: LookupComponent;

  /** å¯¼èªäºä»¶ */
  @Output() selectedChanged: EventEmitter<AuthobjEntity> = new EventEmitter<AuthobjEntity>();


  ngOnInit() {
    this.curKeyIdList = [];
    this.curKeyIdList.push(this.curKeyId);
  }


  constructor() { }

  // åæ°å
  afterKeySelected(args: any) {
      this.curAuthObj = new AuthobjEntity();
      this.curKeyIdList = [];
      // æ´æ°å¼
      from(args).subscribe(
          item => {
                this.curAuthObj.id = item['id'];
                this.curAuthObj.name = item['name'];
                this.curKeyId = item['id'];
                this.curKeyIdList.push(item['id']);
                this.displayText = item['name'];
                this.selectedChanged.emit(this.curAuthObj);
          }
      );
      this.auobjLookup.closeDialog();
  }


  /**
   * éä¸­è¡æ¹ååè§¦åäºä»¶
   */
  handleSelection(args: any) {
    this.curKeyId = args.node.data.id;
    this.curAuthObj = args.node.data;
    // é¦æ¬¡è§¦å
    if (this.curKeyId === '') {
      this.selectedKeyOld = this.curKeyId;
      this.selectedChanged.emit(this.curAuthObj);
    } else {
      // ååçæåµä¸è§¦å
      if (this.selectedKeyOld !== this.curKeyId) {
        this.selectedKeyOld = this.curKeyId;
        this.selectedChanged.emit(this.curAuthObj);
      }
    }
  }
}