import { Component, OnInit, Input, Output, EventEmitter, ViewChild, TemplateRef } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable, from, of } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { TreeNode, TreeTableComponent } from '@farris/ui-treetable';


import { AuthobjEntity } from '../../../common/entity/authobj.entity';
import { AuthObjService } from '../../../common/service/authobj.service';
import { RacCommonUtil } from '../../../common/util/rac-util';

@Component({
  selector: 'rac-auobj-tree',
  templateUrl: './auobj-tree.component.html',
  styleUrls: ['./auobj-tree.component.css']
})
export class AuobjTreeComponent  implements OnInit {

    /** è¦å è½½çæ°æ®ç±»å
     * allä»£è¡¨å¨é¨æéå¯¹è±¡
     * reassignä»£è¡¨åè®¸å¨ç¨æ·ä¸éæ°ææçæéå¯¹è±¡
     */
  @Input()
  dataMode: 'all' | 'reassign';

  @Input()
  initSelectedKeys: string[];

  public data: any[] = [];
  public selectedKey = '';
  selectedKeyOld = '';
  selectedModel: AuthobjEntity;
  // æ æéåæ°
  cols: any[] = [];
  /** æ ç»å®æ°æ®æº */
  treeNodeData: TreeNode[];
  @ViewChild('farrisTt') farrisTt: TreeTableComponent;
  @Output() selectedchanged: EventEmitter<AuthobjEntity> = new EventEmitter<AuthobjEntity>();

  /** å¸®å©è¾åºäºä»¶ */
  @Output() selectedAfterOk: EventEmitter<AuthobjEntity> = new EventEmitter<AuthobjEntity>();
  @Output() closeModalEmitter: EventEmitter<any> = new EventEmitter<any>();

  // æ³¨å¥å°å¸®å©çç¡®è®¤ååæ¶æé®
  @ViewChild('btnOkAndCancle') btnOkAndCancleRef: TemplateRef<any>;

  constructor(
    private service: AuthObjService,
    public racUtil: RacCommonUtil,
    private translate: TranslateService) {
  }

  ngOnInit() {
    this.cols = [
      // { field: 'name', title: this.translate.instant('lblAuthObj') }
      { field: 'name', title: 'åç§°' }
    ];

  }

  /** æ ¹æ®åéçåè½æä½åå§å¯¹åºçæéå¯¹è±¡ */
  initData() {
    let obs = this.service.getAll();
    if ( this.dataMode === 'reassign' ) {
      obs = this.service.getAllReAssign();
    } else {
      obs = this.service.getAll();
    }
    obs.subscribe((result: any) => {
      this.data = result;
      if (this.data) {
        const initNodeList: TreeNode[] = [];
        const layer1 = this.racUtil.findLayer1ByParentId(this.data, 'id', 'parentId');
        layer1.forEach(item => {
          const node = this.convertToTreeNode(item);
          initNodeList.push(node);
          this.initTreeNodeList(node);
        });
        this.treeNodeData = initNodeList;
        this.racUtil.setTreeIcon(this.farrisTt);
      }
    });
  }
  /**
   * æé Farris-Treeæéç»æ
   * @param authfieldList æéå­æ®µåè¡¨
   */
  private initTreeNodeList(parentNode: TreeNode) {
    // æ ¹æ®ç¶èç¹è¿æ»¤å­èç¹
    const children = this.data.filter(x => x.parentId === parentNode.data.id);

    // å°è¿æ»¤çæ°æ®å å°TreeNodesä¸­
    if (children && children.length > 0) {
      children.forEach(item => {
        const child = this.convertToTreeNode(item);
        parentNode.leaf = false;
        parentNode.children.push(child);
        this.initTreeNodeList(child);
      });
    }
  }

  /**
   * å°æéå­æ®µå®ä½è½¬æ¢ä¸ºFarris-TreeNode
   */
  private convertToTreeNode(obj: AuthobjEntity): TreeNode {
    const node = { data: obj, leaf: true, children: [], icon: this.racUtil.leafIcon };
    // æ¯å¦æç»èç¹ï¼typeä¸º0 è¯´ææ¯å¯¼èª
    if (this.racUtil.isNav(obj.type.toString())) {
      // tslint:disable-next-line:no-string-literal
      node['leaf'] = false;
      // tslint:disable-next-line:no-string-literal
      node['children'] = [];
      node['expandedIcon'] = this.racUtil.expandIcon;  // 'folder-open';
      node['collapseIcon'] = this.racUtil.collapseIcon; // 'folder';
    }
    return node;
  }

  appendNode(newObj: AuthobjEntity) {
    const parentRowNode = this.farrisTt.findRowNode(newObj.parentId);
    // const newTreeNode = this.convertToTreeNode(newObj);
    const selectedRow = this.farrisTt.selectedRow;
    if (this.racUtil.isNav( selectedRow.data.type)) {
      this.farrisTt.append( newObj, this.farrisTt.selectedRow);
    } else {
      this.farrisTt.append( newObj , this.farrisTt.selectedRow.parent);
    }
    this.farrisTt.selectNode(newObj.id);
  }

  removeNode(newObj: AuthobjEntity) {
    this.farrisTt.remove(newObj.id);
  }

  updateNode(newObj: AuthobjEntity) {
   // TODO updateæ¶data åæ°æé®é¢
   // this.farrisTt.update(newObj.id, newObj);
  }

  /**
   * éä¸­è¡æ¹ååè§¦åäºä»¶
   */
  handleSelection(args: any) {
    this.selectedKey = args.node.data.id;
    this.selectedModel = args.node.data;
    // é¦æ¬¡è§¦å
    if (this.selectedKeyOld === '') {
      this.selectedKeyOld = this.selectedKey;
      this.selectedchanged.emit(this.selectedModel);
    } else {
      // ååçæåµä¸è§¦å
      if (this.selectedKeyOld !== this.selectedKey) {
        this.selectedKeyOld = this.selectedKey;
        this.selectedchanged.emit(this.selectedModel);
      }
    }
  }

  iconClass({ id }: any): any {
    return {
      'k-i-folder': true,
      'k-icon': true
    };
  }

  /** å¸®å©ç¡®å®æé® */
  confirm() {
    this.selectedKeyOld = this.selectedKey;
    this.selectedAfterOk.emit(this.selectedModel);
  }

  cancle() {
    this.closeModalEmitter.next();
  }
}

