import { Component, OnInit, OnChanges, Input, Output, EventEmitter, ViewChild, TemplateRef, SimpleChanges } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable, from, of } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { ReAssignEntity } from '../../../common/entity/reassign.entity';
import { ReAssignService } from '../../../common/service/reassign.service';

@Component({
  selector: 'rac-reassign-grid',
  templateUrl: './reassign-grid.component.html',
  styleUrls: ['./reassign-grid.component.css']
})
export class ReassignGridComponent implements OnInit, OnChanges {

  columns: any;
  @Input() datas: ReAssignEntity[];
  selectedKey = '';
  selectedKeyOld = '';
  selectedModel: ReAssignEntity;

  @Output() selectedChanged: EventEmitter<ReAssignEntity> = new EventEmitter<ReAssignEntity>();

  /** å¸®å©è¾åºäºä»¶ */
  @Output() selectedAfterOk: EventEmitter<ReAssignEntity> = new EventEmitter<ReAssignEntity>();
  @Output() closeModalEmitter: EventEmitter<any> = new EventEmitter<any>();

  // æ³¨å¥å°å¸®å©çç¡®è®¤ååæ¶æé®
  @ViewChild('btnOkAndCancle') btnOkAndCancleRef: TemplateRef<any>;

  constructor(
    private reAssignService: ReAssignService ) { }

  ngOnInit() {
    this.columns = [
      // { field: 'name', title: this.translate.instant('lblAuthObj') }
      { field: 'authorizationCode', title: 'æéå¯¹è±¡ç¼å·', width: '0' },
      { field: 'authorizationName', title: 'æéå¯¹è±¡åç§°', width: '200' },
      { field: 'authfieldCode', title: 'æéå­æ®µç¼å·', width: '0' },
      { field: 'authfieldName', title: 'æéå­æ®µåç§°' }
    ];
  }

  /** è·ååè®¸éææçæé */
  initData() {
      const obs = this.reAssignService.getAll();
      obs.subscribe((result: any) => {
        this.datas = result;
      });
  }

   /** è¾å¥å±æ§ååå */
   ngOnChanges(changes: SimpleChanges): void {
    if (!this.datas || this.datas.length <= 0) {
      // æå¡ç«¯è·åæ°æ®
      this.initData();
    }
  }

 /**
  * éä¸­è¡æ¹ååè§¦åäºä»¶
  */
  handleSelection(args: any) {
    this.selectedModel = args.data;
    if (!this.selectedModel) {
      this.selectedKey = '';
      return ;
    }
    this.selectedKey = this.selectedModel.id;
    // é¦æ¬¡è§¦å
    if (this.selectedKeyOld === '') {
      this.selectedKeyOld = this.selectedKey;
      this.selectedChanged.emit(this.selectedModel);
    } else {
      // ååçæåµä¸è§¦å
      if (this.selectedKeyOld !== this.selectedKey) {
        this.selectedKeyOld = this.selectedKey;
        this.selectedChanged.emit(this.selectedModel);
      }
    }
  }

  iconClass({ id }: any): any {
    return {
      'k-i-folder': true,
      'k-icon': true
    };
  }

  /** å¸®å©ç¡®å®æé® */
  confirm() {
    this.selectedKeyOld = this.selectedKey;
    this.selectedAfterOk.emit(this.selectedModel);
  }

  cancle() {
    this.closeModalEmitter.next();
  }
}
