    /** æ¾å°æ å½¢Parentåçº§æ¹å¼çå è½½çç¬¬ä¸å± */
    findLayer1ByParentId(datas: any[], key: string, parentKey: string): any[] {
        const rtn = [];
        datas.forEach(data => {
            const findParent = datas.find(parent => parent[key] === data[parentKey]);
            if (!findParent && data.type !== undefined && data.type !== null  && data.type.toString() === '99') {
                data.parentId = undefined;
                rtn.push(data);
            }
        });
        return rtn;
    }

